.swipe {
  /*position: fixed;*/
  padding: 100px 20px;
  height: 100%;
  box-sizing: border-box;
}

.swipe-back {
    position: fixed;
    border-radius: 5vh;
    box-shadow: 0 3px 12px 3px rgba(0,0,0,0.2);
    background-color: white;
    height: 10vh;
    top: 75vh;
    left: 50%;
    transform: translate(-45vh) translateX(-50%) scale(1);
    transition: transform ease-in-out 0.1s
}

.swipe-back-text {
    position: fixed;
    top: 85vh;
    left: 50%;
    font-size: 20px;
    transform: translate(-45vh) translateX(-50%) scale(1);
}

.swipe-back:hover {
    cursor: pointer;
    transform: translate(-45vh) translateX(-50%) scale(1.03);
}

.swipe-favorite {
    position: fixed;
    border-radius: 5vh;
    box-shadow: 0 3px 12px 3px rgba(0,0,0,0.2);
    background-color: white;
    height: 10vh;
    top: 75vh;
    left: 50%;
    transform: translate(45vh) translateX(-50%) scale(1);
    transition: transform ease-in-out 0.1s
}

.swipe-favorite-text {
    position: fixed;
    top: 85vh;
    left: 50%;
    font-size: 20px;
    transform: translate(45vh) translateX(-50%) scale(1);
}

.swipe-favorite:hover {
    cursor: pointer;
    transform: translate(45vh) translateX(-50%) scale(1.03);
}

.swipe-card {
  width: 50vh;
  height: 80vh;
  min-height: 480px;
  min-width: 300px;
  border-radius: 20px;
  overflow: hidden;
  position: fixed;
  left: 50%;
  z-index: 100;
  /* left: 50%; */

  box-shadow: 0 3px 12px 3px rgba(0,0,0,0.2);
  background-color: white;
}


.swipe-card:hover {
  cursor: pointer;
}

.swipe-no-cards {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 40vh;
    transform: translate(-50%, -50%);
}

  .swipe-card-dislike {
      width: 55%;
      right: 0;
      position: absolute;
  }

  .swipe-card-like {
      width: 55%;

      position: absolute;
  }

  .swipe-card-image {
      height: 60vh;
      min-height: 360px;

  }

  .swipe-card-title {
      text-align: center;
  }

      .swipe-card-info {
          width: 6%;
          transform: translate(0,-5%);
          transition: transform ease-in-out 0.1s;
          margin: 10px auto;
          left:50%

      }

      .swipe-card-info:hover {
          transform: translate(0,-5%) scale(1.1);
      }

  .swipe-card-bot {
      padding: 0 20px;
      box-sizing: border-box;
  }


      .swipe-people {
      }
      .swipe-type {
          margin: 0;
      }

      img {
          -webkit-user-select: none;
          -khtml-user-select: none;
          -moz-user-select: none;
          -o-user-select: none;
          user-select: none;
          }

              .fade-enter {
                  opacity: 0.01;
              }
              .fade-enter-active {
                  opacity: 1;
                  transition: opacity 500ms ease-in;
              }
              .fade-exit {
                  opacity: 1;
              }
              .fade-exit-active {
                  opacity: 0.01;
                  transition: opacity 500ms ease-in;
              }

.swipe-img {
  height: 170px;
  width: 170px;
}
